/* Basic settings */
html {
  font-size: 10px;
}

.page {
  font-family: 'HN', Arial, sans-serif;
  font-weight: 400;
  font-size: 1.7rem;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

button:active, button:focus, input:active, input:focus, textarea:active, textarea:focus, a:active, a:focus {
  outline: none;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.section-header__h2 {
  font-size: 3.5rem;
  font-weight: 900;
  display: inline-block;
}

.section-header__h2::after {
  content: '';
  display: block;
  margin: auto;
  margin-top: 1rem;
  width: 5.6rem;
  height: 0.5rem;
  background-color: #18a7d0;
}

.section-header__h3 {
  font-size: 1.6rem;
}

.button [class*='-btn'] {
  cursor: pointer;
  display: inline-block;
  padding: 2.1rem 4.3rem;
  border: none;
  border-radius: 3px;
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: bold;
  background-color: #91c82c;
  color: white;
  text-shadow: 0 1px 2px #578f22;
}

@media screen and (max-width: 768px) {
  .button [class*='-btn'] {
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 544px) {
  .button [class*='-btn'] {
    padding: 1.7rem 3.3rem;
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 420px) {
  .button [class*='-btn'] {
    padding: 1.1rem 1.7rem;
    font-size: 1rem;
  }
}

.debug-grid {
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.debug-grid > div {
  height: inherit;
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.debug-grid > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug-grid > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(8.33333% - 20px);
  height: inherit;
  outline: #000 1px solid;
  background: rgba(255, 0, 0, 0.2);
}
